@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
@use "sass:list";
body {
  font-size: 14px;
  font-family: Lora, serif; }

* {
  box-sizing: border-box; }

.root {
  min-width: 135px;
  box-sizing: border-box; }

.select {
  width: 100%;
  height: 36px;
  display: flex;
  align-items: center;
  background-color: #B09A81;
  border-radius: 4px;
  color: #462929;
  padding: 12px;
  box-sizing: border-box; }
  .select .count {
    border-radius: 4px;
    background-color: #462929;
    color: #B09A81;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: 5px;
    padding: 3px 6px;
    font-size: 10px; }
  .select .caret {
    transition: border-color 0.1s ease-in-out;
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: auto;
    vertical-align: middle;
    border-top: 4px dashed #796A58;
    border-top: 4px solid\9  #796A58;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent; }
  .select:hover .caret {
    border-top: 4px dashed #462929;
    border-top: 4px solid\9  #462929;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent; }
  .select_menuOpen .caret {
    border-top: none !important;
    border-bottom: 4px dashed #462929;
    border-bottom: 4px solid\9  #462929;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent; }

.menu {
  position: absolute;
  z-index: 100;
  background-color: #B09A81;
  border-radius: 4px;
  padding: 20px 14px;
  margin-top: 10px;
  box-sizing: border-box; }
  .menu .option:not(:first-child):not(:last-child) {
    margin: 8px 0; }
