@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
@use "sass:list";
body {
  font-size: 14px;
  font-family: Lora, serif; }

* {
  box-sizing: border-box; }

.root {
  cursor: pointer;
  transition: color 0.1s ease-in-out; }
  .root_disabled {
    color: #796A58 !important;
    cursor: default !important; }
  .root_primary {
    color: #B09A81; }
    .root_primary:hover {
      color: #DAAF7C; }
    .root_primary:active {
      color: #B08F67; }
  .root_green {
    color: #1F4F3B; }
    .root_green:hover {
      color: #2D653A; }
    .root_green:active {
      color: #296036; }
