@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
@use "sass:list";
body {
  font-size: 14px;
  font-family: Lora, serif; }

* {
  box-sizing: border-box; }

.root {
  height: 36px;
  display: flex;
  align-items: center;
  background-color: #B09A81;
  border-radius: 4px;
  padding: 12px;
  width: 100%;
  box-sizing: border-box; }
  .root .input {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border-radius: 0;
    border: none;
    background-color: transparent;
    width: 100%;
    font-size: 16px;
    color: #462929;
    font-family: inherit; }
    .root .input::placeholder {
      color: #796A58; }
  .root .endAdornment {
    margin-left: 6px;
    max-width: 16px;
    max-height: 16px; }
  .root .startAdornment {
    margin-right: 6px;
    max-width: 20px;
    max-height: 20px; }
  .root_large {
    border-radius: 8px;
    height: 50px;
    padding: 14px; }
    .root_large .input {
      font-size: 18px; }
    .root_large .endAdornment {
      margin-left: 10px;
      max-width: 22px;
      max-height: 22px; }
    .root_large .startAdornment {
      margin-right: 10px;
      max-width: 22px;
      max-height: 22px; }
