@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
@use "sass:list";
body {
  font-size: 14px;
  font-family: Lora, serif; }

* {
  box-sizing: border-box; }

.root {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  transition: background-color 0.1s ease-in-out;
  padding: 15px 30px;
  font-size: 18px;
  border-radius: 8px; }
  .root_large {
    font-size: 20px;
    padding: 17px 30px; }
  .root_small {
    font-size: 14px;
    padding: 6px 24px; }
  .root_primary {
    background-color: #B09A81;
    color: #462929; }
    .root_primary:hover {
      background-color: #DAAF7C; }
    .root_primary:active {
      background-color: #B08F67; }
  .root_green {
    background-color: #1F4F3B;
    color: #B09A81; }
    .root_green:hover {
      background-color: #2D653A; }
    .root_green:active {
      background-color: #296036; }
