@import url("https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap");
@use "sass:list";
body {
  font-size: 14px;
  font-family: Lora, serif; }

* {
  box-sizing: border-box; }

.root {
  min-height: 50px;
  background-color: #B09A81;
  border-radius: 4px;
  padding: 12px;
  width: 100%;
  box-sizing: border-box; }
  .root .textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border-radius: 0;
    border: none;
    background-color: transparent;
    width: 100%;
    height: 100%;
    font-size: 16px;
    color: #462929;
    font-family: inherit; }
    .root .textarea::placeholder {
      color: #796A58; }
